// Substitua esta função por integração real com OCR (Tesseract, Vision API, etc.)
export async function extractReadingFromImage(_file: File): Promise<number> {
  // Simulação: gera um valor plausível a partir de timestamp para ser determinístico
  await new Promise((r) => setTimeout(r, 300));
  const base = Math.floor(Date.now() / 100000) % 1000;
  return 1000 + base;
}
