"use client";
import { useEffect, useRef, useState } from "react";
import { addReading, getLastReading } from "@lib/storage";
import { extractReadingFromImage } from "@lib/ocrMock";

type Props = {
  isOpen: boolean;
  onClose: () => void;
  onAdded: () => void;
};

export default function AddReadingModal({ isOpen, onClose, onAdded }: Props) {
  const [mode, setMode] = useState<"none" | "manual" | "ocr">("none");
  const [manualValue, setManualValue] = useState<string>("");
  const [detectedValue, setDetectedValue] = useState<number | null>(null);
  const [geo, setGeo] = useState<{ lat: number; lng: number } | undefined>(undefined);
  const [error, setError] = useState<string | null>(null);
  const [last, setLast] = useState<ReturnType<typeof getLastReading>>(null);
  const cameraInputRef = useRef<HTMLInputElement>(null);
  const galleryInputRef = useRef<HTMLInputElement>(null);
  const [present, setPresent] = useState(false);
  const [show, setShow] = useState(false);
  const [digits, setDigits] = useState<string[]>(Array(6).fill(""));
  const digitRefs = useRef<Array<HTMLInputElement | null>>([]);

  useEffect(() => {
    if (isOpen) {
      setMode("none");
      setManualValue("");
      setDetectedValue(null);
      setGeo(undefined);
      setError(null);
      
      setLast(getLastReading());
      setDigits(Array(6).fill(""));
      setPresent(true);
      requestAnimationFrame(() => setShow(true));
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (pos) => {
            setGeo({ lat: pos.coords.latitude, lng: pos.coords.longitude });
          },
          () => {
            setGeo(undefined);
          },
          { enableHighAccuracy: true }
        );
      }
    } else {
      setShow(false);
    }
  }, [isOpen]);

  const onFileChange = async (f: File | null) => {
    setDetectedValue(null);
    setError(null);
    if (f) {
      setMode("ocr");
      const value = await extractReadingFromImage(f);
      setDetectedValue(value);
      
      }
  };

  const updateManualFromDigits = (arr: string[]) => {
    const joined = arr.join("");
    const num = joined && /^\d+$/.test(joined) ? Number(joined) : NaN;
    setManualValue(joined);
    if (Number.isFinite(num) && last && num <= last.reading) {
      setError("A nova leitura deve ser maior que a última leitura registrada");
    } else {
      setError(null);
    }
  };

  const handleDigitChange = (i: number, v: string) => {
    const d = v.replace(/\D/g, "");
    const next = [...digits];
    next[i] = d.slice(-1) || "";
    setDigits(next);
    updateManualFromDigits(next);
    if (d && digitRefs.current[i + 1]) digitRefs.current[i + 1]?.focus();
  };

  const handleDigitKeyDown = (i: number, e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Backspace" && !digits[i] && digitRefs.current[i - 1]) {
      e.preventDefault();
      digitRefs.current[i - 1]?.focus();
    }
    if (e.key === "ArrowLeft" && digitRefs.current[i - 1]) {
      e.preventDefault();
      digitRefs.current[i - 1]?.focus();
    }
    if (e.key === "ArrowRight" && digitRefs.current[i + 1]) {
      e.preventDefault();
      digitRefs.current[i + 1]?.focus();
    }
  };

  const currentValue = mode === "manual" ? Number(manualValue) : detectedValue ?? null;
  const canSave = currentValue !== null && Number.isFinite(currentValue) && currentValue > 0 && (!last || currentValue > (last?.reading ?? 0));

  const submit = () => {
    const value = currentValue ?? 0;
    if (!Number.isFinite(value) || value <= 0) {
      setError("Informe uma leitura válida");
      return;
    }
    if (last && value <= last.reading) {
      setError("A nova leitura deve ser maior que a última leitura registrada");
      return;
    }
    addReading(value, geo);
    onAdded();
  };

  if (!present) return null;

  return (
    <div
      className={`fixed inset-0 z-50 flex items-end sm:items-center justify-center p-2 sm:p-4 transition-opacity duration-300 ease-out ${show ? "bg-black/30 backdrop-blur-sm opacity-100" : "bg-black/30 backdrop-blur-sm opacity-0"}`}
      onTransitionEnd={() => { if (!show) setPresent(false); }}
    >
      <div className={`card w-full max-w-xl transform transition-all duration-300 ${show ? "opacity-100 translate-y-0 scale-100 ease-out" : "opacity-0 translate-y-4 scale-95 ease-in"}`}>
        <div className="mb-4">
          <h3 className="text-xl font-semibold text-water-blue">Nova leitura</h3>
        </div>

        <div className="flex flex-col items-center gap-3">
          {mode === "manual" ? (
            <div className="w-full max-w-2xl">
              <div className="mb-2 text-center text-slate-600">Última leitura: {last?.reading ?? "-"}</div>
              <div className="grid grid-cols-6 gap-2 justify-items-center">
                {digits.map((d, i) => {
                  const isRed = i >= digits.length - 2;
                  return (
                    <div key={i} className="w-12 h-16 rounded-md bg-black flex items-center justify-center shadow-soft">
                      <input
                        ref={(el) => (digitRefs.current[i] = el)}
                        value={d}
                        onChange={(e) => handleDigitChange(i, e.target.value)}
                        onKeyDown={(e) => handleDigitKeyDown(i, e)}
                        inputMode="numeric"
                        maxLength={1}
                        className={`bg-transparent text-center w-full outline-none text-3xl font-bold ${isRed ? "text-red-600" : "text-white"}`}
                      />
                    </div>
                  );
                })}
              </div>
            </div>
          ) : (
            <div className="w-full max-w-2xl grid grid-cols-2 gap-3">
              <div
                className="rounded-xl border-2 border-dashed border-water-light bg-water-gray/50 hover:bg-water-light transition p-8 text-center cursor-pointer flex flex-col items-center justify-center"
                onClick={() => cameraInputRef.current?.click()}
                onDragOver={(e) => e.preventDefault()}
                onDrop={(e) => { e.preventDefault(); const f = e.dataTransfer.files?.[0] ?? null; onFileChange(f); }}
              >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="w-12 h-12 text-water-blue">
                  <path strokeWidth="1.5" d="M9 7l1.5-2h3L15 7h2a3 3 0 013 3v6a3 3 0 01-3 3H7a3 3 0 01-3-3V10a3 3 0 013-3h2z"/>
                  <circle cx="12" cy="13" r="4" strokeWidth="1.5" />
                </svg>
                <div className="mt-2 font-medium text-slate-700">Ler pela câmera</div>
              </div>
              <div
                className="rounded-xl border-2 border-dashed border-water-light bg-water-gray/50 hover:bg-water-light transition p-8 text-center cursor-pointer flex flex-col items-center justify-center"
                onClick={() => setMode("manual")}
              >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" className="w-12 h-12 text-water-blue">
                  <rect x="4" y="4" width="6" height="6" rx="1.5" strokeWidth="1.5" />
                  <rect x="14" y="4" width="6" height="6" rx="1.5" strokeWidth="1.5" />
                  <rect x="4" y="14" width="6" height="6" rx="1.5" strokeWidth="1.5" />
                  <rect x="14" y="14" width="6" height="6" rx="1.5" strokeWidth="1.5" />
                </svg>
                <div className="mt-2 font-medium text-slate-700">Digitar manualmente</div>
              </div>
            </div>
          )}

          <input
            ref={cameraInputRef}
            type="file"
            accept="image/*"
            capture="environment"
            className="hidden"
            onChange={(e) => onFileChange(e.target.files?.[0] ?? null)}
          />
          

          {detectedValue !== null && (
            <div className="text-sm text-slate-700">Leitura detectada: <span className="font-semibold">{detectedValue}</span></div>
          )}
          {geo && (
            <div className="text-sm text-slate-600">Lat: {geo.lat.toFixed(6)} | Lng: {geo.lng.toFixed(6)}</div>
          )}
          {error && (
            <div className="w-full max-w-xl rounded-xl bg-red-50 border border-red-300 text-red-700 p-3">{error}</div>
          )}
        </div>

        <div className="mt-6 space-y-2">
          <button className="btn btn-primary w-full" onClick={submit} disabled={!canSave}>Salvar leitura</button>
          <button className="btn bg-slate-100 w-full" onClick={onClose}>Cancelar</button>
        </div>
      </div>
    </div>
  );
}
